import { API } from "aws-amplify";

const API_NAME = "sportEquipmentApi";

// Fonctions pour les produits
export const getProducts = async () => {
  try {
    return await API.get(API_NAME, "/products", {});
  } catch (error) {
    console.error("Erreur lors de la rcupration des produits:", error);
    throw error;
  }
};

export const getProduct = async (productId) => {
  try {
    return await API.get(API_NAME, `/products/${productId}`, {});
  } catch (error) {
    console.error(
      `Erreur lors de la rcupration du produit ${productId}:`,
      error
    );
    throw error;
  }
};

export const createProduct = async (productData) => {
  try {
    return await API.post(API_NAME, "/products", {
      body: productData,
    });
  } catch (error) {
    console.error("Erreur lors de la cration du produit:", error);
    throw error;
  }
};

export const updateProduct = async (productId, productData) => {
  try {
    return await API.put(API_NAME, `/products/${productId}`, {
      body: productData,
    });
  } catch (error) {
    console.error(
      `Erreur lors de la mise  jour du produit ${productId}:`,
      error
    );
    throw error;
  }
};

export const deleteProduct = async (productId) => {
  try {
    return await API.del(API_NAME, `/products/${productId}`, {});
  } catch (error) {
    console.error(
      `Erreur lors de la suppression du produit ${productId}:`,
      error
    );
    throw error;
  }
};

